function MoreText(button, text) {
  this.text = text;
  this.button = button;
}

MoreText.prototype.requestForm = function requestForm() {
  this.button.addEventListener('click', () => {
    document.querySelector('.feedback__wrapper').scrollIntoView({block: 'center', inline: 'center'});
  });
};

MoreText.prototype.openText = function openText() {

  this.button.addEventListener('click', () => {
    if (this.text.style.display === 'block') {
      this.text.style.display = 'none';
    } else {
      this.text.style.display = 'block';
    }
  });

};
export const toScroll = new MoreText(document.querySelector('.button--request-js'));
export const handler = new MoreText(document.querySelector('.button--moretext-js'), document.querySelector('.moretext-js'));

